* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  line-height: 1.5;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

:root {
  --color-bg: #ecf2f8;
  --color-white: #fff;
  --color-violet: #7541c8;
  --color-light-grey: #cfcfcf;
  --color-dark-grey: #19212e;
  --color-grey: #48556a;
}

html,
body,
header {
  height: 100%;
}

header {
  overflow: hidden;
}

.hero {
  height: 100%;
  background: url("../images/hero1.jpg") no-repeat center center fixed;
  background-size: cover;
}

.gallery__carousel img {
  height: 240px;
  object-fit: cover;
}

.footer__title {
  position: relative;
}
.footer__title::before {
  content: "";
  position: absolute;
  display: block;
  background-color: #eee;
  height: 1px;
  width: 60px;
  margin-top: 8px;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -100%);
}
@media (min-width: 768px) {
  .footer__title::before {
    top: 100%;
    left: 0;
    transform: translate(0, -100%);
  }
}